import { withRouter } from 'next/router';

export default function Navbar() {
    return (
        <>
            <div>
                Objection
            </div>
            <style jsx>{`
                div {
                    position: relative;
                    z-index: 1;
                    left: 0;
                    box-sizing: border-box;
                    width: 100%;
                    height: 40px;
                    font-family: Verdana, sans-serif;
                    padding: 14px 16px;
                    background-color: #f1f1f1;
                    box-shadow: 0px 4px 4px 0px #ddd;
                }
            `}</style>
        </>
    );
}